<template>
	<div class="detail">
		<van-nav></van-nav>
		<div class="content">
			<div class="header clear">
				<h2>{{detail.zuhe}}</h2>
				<p>{{detail.dataTitle}}</p>
			</div>
			<div class="cont">
				<h3>{{detail.dataName}}</h3>
				<div class="time">
					<p>{{detail.original}} <span>现价：{{detail.price}}</span></p>
				</div>
				<div class="text-box">
					<img :src="detail.dataImg" alt="">
				</div>
			</div>
		</div>

	
	</div>
</template>

<script>
	import vanNav from '../components/van-nav/van-nav.vue'
	export default {
		name: 'detail',
		data() {
			return {
			detail:[]
			}
		},
		components: {
			vanNav
		},
		mounted() {
			
  
      let _id = this.$route.params._id;
      let collectionName = this.$route.query.collectionName;
      
      console.log(_id,collectionName);
      
      this.$axios.get(`/news/${collectionName}/${_id}`).then(
        res=>{
    
          this.detail=res.data.data
		 
        }
      )
		},

		updated() {

		},
		methods: {

		},
		computed: {}
	}
</script>

<style>
	.detail {
		margin-top: 50px;
	}
	.time span{
		color: red;
	}
</style>
