<template>
  <div id="app">
	 <van-loading color="#1989fa" class="loding" v-if="$root.bLoding"/>
	  <!-- <van-header v-show="$root.bNav"></van-header> -->
	  <router-view></router-view>
	  <van-footer v-show='$root.bFoot'></van-footer>
	 </div>
</template>

<script>
	/*  定义组件*/
/* import vanHeader from './header.vue' */
import vanFooter from './footer.vue'
export default {
  name: 'App',
   data() {
      return {
      
      };
    },
  components: {
	  /* 注册组件*/
	
	vanFooter
  },
 
  methods:{
	
  },
  
  watch:{
	 $route:{
	   handler(nextValue){
	     console.log('watch');
	     let path = nextValue.path;//String
	     if(/home|/.test(path)){
	       this.$root.bNav=this.$root.bFoot=true;
	     }
	     if(/login|reg|follow|detail/.test(path)){
	       this.$root.bNav=this.$root.bFoot=false;
	     }
	     if(/user|column/.test(path)){
	       this.$root.bNav=false;
	       this.$root.bFoot=true;
	     }
	   },
	   immediate:true
	 } 
  }
}
</script>

<style scoped>
  .footer{
	  width: 100%;
	  height: 60px;
	  
  }
  .loding{
	  margin-top: 50%;
	  margin-left: 45%;
	  position: absolute;
  }
  #app{
	  position: relative;
  }
</style>
