import axios from "../../plugins/axios.js"
let state ={
	list:{}	
}

let actions ={
  	list:({commit})=>{
	axios({
			url: '/news/list',
			params: {
				_limit: 8
			}
		}).then(res => {
		
			commit('list',res.data.data)
			/* console.log(this.msg); */
		})
		
		}
		
	
}

let mutations ={
	/* mutations里只有两个参数，一个state，一个接收的参数 */
	list:(state,payload)=>{
		state.list=payload
		
	}
}
	

export default{
	namespaced:true,
	state,actions,mutations
}