<template>
	<div class="login">
		<van-grid :border="false" :column-num="1" round>


			<van-grid-item>
				<img src="../assets/imags/touxiang.png" alt="" width='200px'>
			</van-grid-item>
		</van-grid>
		<van-form label-align='center'>
			<van-field v-model="username" name="用户名" label="用户名" placeholder="用户名"
				:rules="[{ required: true, message: '请填写用户名' }]" />
			<van-field v-model="password" type="password" name="密码" label="密码" placeholder="密码"
				:rules="[{ required: true, message: '请填写密码' }]" />
			<div style="margin: 16px;">
				<van-button round block type="primary" size="large" @click='login'>登录</van-button>
			</div>
			<van-button round block to='/reg'>去注册</van-button>
		</van-form>

	</div>
</template>

<script>
	export default {
		name: 'login',
		props: {},
		data() {
			return {
				username: '',
				password: '',
				mess:''
			}
		},
		components: {},
		mounted() {},
		updated() {},
		methods: {
			login() {
				console.log(1);
				this.$axios({
					url: 'login',
					method: 'post',
					data: {
						username: this.username,
						password: this.password
					}
				}).then(res => {
					console.log(res.data.err);
					if (res.data.err === 0) {
						window.localStorage.setItem('user', JSON.stringify(res.data))
						if (this.$route.query.p) {
							this.$router.push(this.$route.query.p)
						} else {
							this.$router.push('/home')
							
						}
					}else{
						this.mess=res.data.msg
						this.$router.push('/reg')
					}
				})
			}
		},
		computed: {}
	}
</script>

<style>
	.login {}
</style>
