import axios from "../../plugins/axios.js"
let state ={
	banner:{}	
}

let actions ={
  	banner:async ({commit})=>{
	let res=await axios({
			url: '/news/banner',
			params: {
				_limit: 10
			}
		})
		
		commit('banner',res.data.data)
	
}
}
let mutations ={
	/* mutations里只有两个参数，一个state，一个接收的参数 */
	banner:(state,payload)=>{
		state.banner=payload
		
	}
}
	

export default{
	namespaced:true,
	state,actions,mutations
}