<template>
	<div class="follow">


		<van-nav></van-nav>
		<div class="aa">

			<h3>我在这里等着你</h3>
			<div id="baidu"></div>
			<input type="text" v-model="sou"><button @click="suo">搜索</button>
		</div>
		<div id="main" style="width: 600px;height:400px;"></div>
	</div>
</template>

<script>
	import vanNav from '../components/van-nav/van-nav.vue'


	export default {
		name: 'follow',
		components: {
			vanNav
		},
		data() {
			return {
				sou: '',
			}
		},
		methods: {
			suo() {
				/* var local = new BMapGL.LocalSearch(this.map, {
						renderOptions:{map: this.map}
					});
					local.search(this.sou); */
				var local = new BMapGL.LocalSearch(this.map, {
					renderOptions: {
						map: this.map
						
					}
				});
				local.searchNearby(this.sou, this.mPoint, 1000);
			}
		},
		mounted() {
			this.map = new BMapGL.Map('baidu'); // 创建Map实例
			this.map.centerAndZoom(new BMapGL.Point(121.491524, 31.378712), 15);
			this.map.enableScrollWheelZoom(true); // 初始化地图,设置中心点坐标和地图级别
			this.marker1 = new BMapGL.Marker(new BMapGL.Point(121.491524, 31.378712));
			this.map.addOverlay(this.marker1);
			this.mPoint = new BMapGL.Point(121.491524, 31.378712);

		}
	}
</script>

<style>
	.aa {
		margin-top: 100px;

	}

	#baidu {
		height: 300px;

	}
</style>
