<template>
  <div class="reg">
    <van-grid :border="false" :column-num="1" round>
       
       
        <van-grid-item>
         	<img src="../assets/imags/touxiang.png" alt="" width='200px' @click="upload">
			<span>点击头像上传头像</span>
        </van-grid-item>
		 <input type="file" id="icon" style="display: none;">
      </van-grid>
    <van-form  label-align='center' >
      <van-field
        v-model="username"
        name="用户名"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="密码"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <div style="margin: 16px;">
        <van-button round block type="info" size="large" @click="reg">注册</van-button>
      </div>
	  <van-button round block  size="large" to='/login'>去登录</van-button>
    </van-form>
  </div>
</template>

<script>
  export default {
    name:'reg',
    props:{},
    data(){
      return {
        username:'',
		password:'',
		mess:''
      }
    },
    components:{},
    mounted(){},
    updated(){},
    methods:{
		upload(){
			let inputfile = document.getElementById('icon');
			  inputfile.click()
			},
			reg(){
			  
			 let fileInput = document.getElementById('icon');
			  let formData = new FormData();
			  formData.append('username',this.username);
			  formData.append('password',this.password);
			  formData.append('icon',fileInput.files[0]);
			  console.log(fileInput.files[0]);
			  formData.append('nikename','赟哥好帅')
			  
			  this.$axios({
			    url:'reg',
			    method:'post',
			    data:formData
			  }).then(
			    res=>{
			      // console.log(0.5,res)
			      if(res.data.err===0){
			        this.$router.push('/login')
			      }else{
			        this.mess = res.data.msg
			      }
			    }
			  )
			  
			  console.log('reg');
			}
		}
	}
   
  
</script>

<style>
  .reg{}
</style>
