<template>
	<div class="van-swiper"  v-if="msg">
		<van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
			
				<div v-for="(val,k) in msg" :key='val._id'>
					<van-swipe-item>
				<img :src="val.dataImg" />
				<span>{{val.dataName}}</span>
				</van-swipe-item>
				</div>
			
			
		</van-swipe>
	</div>
</template>

<script>
	// import vanSwiper from './van-swiper.vue'
	import {
		Swiper,
		SwiperSlide
	} from 'swiper/vue';

	export default {
		name: 'van-swiper-text',
		components: {
			Swiper,
			SwiperSlide
		},
		data(){
			 return {msg:[]}
		},
		mounted() {
			this.$axios({
				url:'/news/banner',
				params:{
					_limit:4
				}
			}).then(res=>{
				this.msg=res.data.data
				
			})
		}
	}
</script>

<style scoped>
	.van-swipe-item {
		position: relative;
	}
	img{
		width: 100%;
		height: 300px;
	}
	span{
		position: absolute;
		left: 150px;
		bottom: 50px;
		color: #3f3f3f;
		font-size: 30px;
		
	}
</style>
