import axios from "../../plugins/axios.js"
let state ={
	user:{},
	msg:{}
}

let actions ={
	column:({commit})=>{
		Promise.all([axios.get('/user'),axios({url:'/news/banner',
		params: {
			_limit: 10
		}
		})])
		.then(
		  res=>{
			 /* this.user=res[0].data.data
			  this.msg=res[1].data.data */
			 commit('column',res[0].data.data)
			  
			
			  console.log(state.user);
		 }
		)
	}
}

let mutations ={
	/* mutations里只有两个参数，一个state，一个接收的参数 */
	column(state,payload){
		state.user=payload
	}
}
	

export default{
	namespaced:true,
	state,actions,mutations
}